<template>
  <div>
    <header class="header">
      <div class="title-container">
        <el-menu 
          :default-active="activeIndex"
          class="el-menu-container"
          mode="horizontal"
          @select="handleSelect"
          background-color="#000"
          text-color="#ffffffbf"
          active-text-color="#fff"
        >
        <span class="title">ShadowSocks 免费账号下载</span>        
        <el-menu-item index="1">
          <router-link to="/">主页
          </router-link>
          </el-menu-item>
        <el-submenu index="2">
          <template slot="title">客户端下载</template>
          <el-menu-item index="2-1">
            <a href="https://github.com/shadowsocks/ShadowsocksX-NG/releases/" target="_blank">Mac OS</a>
            </el-menu-item>
          <el-menu-item index="2-2">
            <a href="https://github.com/shadowsocks/shadowsocks-windows/releases" target="_blank">Windows</a>                      
          </el-menu-item>
          <el-menu-item index="2-3">
            <a href="https://itunes.apple.com/cn/app/superwingy/id1290093815?mt=8" target="_blank">iOS</a>                                  
          </el-menu-item>
          <el-menu-item index="2-4">
            <a href="https://github.com/shadowsocks/shadowsocks-android/releases" target="_blank">Android</a>                              
          </el-menu-item>          
        </el-submenu>
        <el-menu-item index="3"><a href="https://github.com/phobal/ShadowSocksSpider" target="_blank">Github</a></el-menu-item>
        </el-menu>
      </div>
    </header>
    <!-- <div class="qrcode">
      <p style="font-size: 12px">关注微信公众号及时获取最新消息</p>
      <img height="100%" width="100%" src="../assets/getqrcode.jpeg"/>
    </div> -->
    <div class="list">
      <el-table
      :data="iplist"
      :height="height"
      border
      style="width: 100%;">
        <el-table-column
          prop="remarks"
          label="名称">
        </el-table-column>
        <el-table-column
          prop="server"
          label="IP地址">
        </el-table-column>
        <el-table-column
          prop="server_port"
          label="端口">
        </el-table-column>
        <el-table-column
          prop="password"
          label="密码">
        </el-table-column>
            <el-table-column
          prop="method"
          label="方法">
        </el-table-column>
        <el-table-column
          prop="time"
          label="更新时间">
        </el-table-column>
      </el-table>
      <div>
        <h4>使用说明: <span style="color: red; font-size: 12px">最近元数据网站防爬策略增强了，数据可能更新是那么及时了，大家耐心多试几个账号</span></h4>
        <p>点击右侧下载按钮，等待下载结束后将下载后的 ss.json 导入到 shadowsocks 
          <a href="https://github.com/shadowsocks/ShadowsocksX-NG/releases/" target="_blank">mac</a>
          <a href="https://github.com/shadowsocks/shadowsocks-windows/releases" target="_blank">Windows</a>  
          <a href="https://itunes.apple.com/cn/app/superwingy/id1290093815?mt=8" target="_blank">iOS</a>                            
          <a href="https://github.com/shadowsocks/shadowsocks-android/releases" target="_blank">Android</a>                  
          <span>客户端中即可</span>
        </p>
      </div>
    </div>
    <div class="download">
      <a href="http://118.24.28.106:3000/download" type="download">
        <i class="el-icon-download"></i>
      </a>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "IPList",
  data() {
    return {
      iplist: [],
      height: 600,
      url: "118.24.28.106:3000",
      activeIndex: '1',
      // url: 'localhost:3000',
    };
  },
  created() {
    this.gData();
  },
  methods: {
    gData() {
      axios.get(`http://${this.url}/data`).then(res => {
        this.iplist = res.data;
      });
    },
    handleSelect() {}
  }
};
</script>

<style>
.list {
  max-width: 80%;
  margin: 0 auto;
  padding-top: 60px;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #000;
  z-index: 2;
}
.title-container {
  padding: 0 10%;
}
.el-menu-container {
    background: transparent;
    display: flex;
    justify-content: flex-end;
    position: relative;
}
.title {
  color: #fff;
  font-size: 18px;
  line-height: 60px;
  position: absolute;
  left: 0;
}
.el-icon-download {
  color: #fff;
  font-size: 36px;
}
.download {
  width: 60px;
  height: 60px;
  position: absolute;
  background: #000;
  right: 5%;
  bottom: 5%;
  z-index: 3;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.download:hover {
  background: #554d4d;
}
.qrcode {
  width: 100px;
  height: 100px;
  position: fixed;
  z-index: 3;
  right: 20px;
  top: 70px;
}
</style>




